.info__photo-edit {
    max-height: 26px;
    max-width: 26px;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-size: cover;
    position: absolute;
    background-color: rgba(0, 0, 0, 0);
    padding: 100%;
    opacity: 0;
    -webkit-transition: background-color 0.5s ease-in-out, opacity 0.3s ease;
    -moz-transition: background-color 0.5s ease-in-out, opacity 0.3s ease;
    -ms-transition: background-color 0.5s ease-in-out, opacity 0.3s ease;
    -o-transition: background-color 0.5s ease-in-out, opacity 0.3s ease;
    transition: background-color 0.5s ease-in-out, opacity 0.3s ease;
}

.info__photo-edit:hover {
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 1;
}

@media screen and (max-width: 640px) {
    .info__photo {
        margin: 0;
    }
}